import { defineComponent } from '@vue/composition-api';
import i18n from 'i18next';

const searchbar = defineComponent({
  props: {
    keyword: {
      type: String as () => string | null,
      // default: null,
    },
  },
  render() {
    const self = this as InstanceType<typeof searchbar>;
    return <el-input size='small' value={self.keyword}
    oninput={(val: string) => self.$emit('change', val)}
    prefix-icon="el-icon-search" placeholder={i18n.t('supplier-backend.preview.search')}>
    </el-input>;
  },
});

export default searchbar;
